@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
.header{display:flex;flex-grow:1;margin:0;padding:10px 15px;background-color:var(--header-bg-color)}.header__items{display:flex;justify-content:space-evenly;flex-grow:1}.header__item{display:flex;align-items:center;flex-direction:column;font-family:'PT Sans', sans-serif;font-size:20px;line-height:22px;font-weight:400;text-align:center}.item__icon{display:block;width:45px;height:45px}.item__title{display:block;margin-top:5px;font-size:17px;letter-spacing:0.5px}.item__figure{display:block;margin-top:5px;padding:6px 11px;font-size:18px;font-weight:700;letter-spacing:0.5px;background-color:#e7e7e7;border:1px solid #727272;border-radius:25px}.item__figure--updating{-webkit-filter:blur(5px);filter:blur(5px)}

.coins-filter{position:-webkit-sticky;position:sticky;display:flex;top:0;padding:0;background-color:var(--coins-filter-bg-color)}.coins-filter__items{display:flex;flex-direction:row;justify-content:center;flex-grow:1}.coins-filter__item{display:block;padding:15px 0;font-family:'PT Sans', sans-serif;font-size:20px;line-height:22px;font-weight:400;text-align:center;font-weight:700;font-size:18px;line-height:20px;text-align:center;transition:.5s ease;border-left:1px solid #dfdfdf}.coins-filter__item:nth-child(1){width:8%}.coins-filter__item:nth-child(1):hover{background-color:transparent;cursor:default}.coins-filter__item:nth-child(2){width:12%}.coins-filter__item:nth-child(3){width:10%}.coins-filter__item:nth-child(4){width:14%}.coins-filter__item:nth-child(5){width:14%}.coins-filter__item:nth-child(6){width:15%}.coins-filter__item:nth-child(7){width:8%}.coins-filter__item:nth-child(8){width:8%}.coins-filter__item:nth-child(9){width:10%;border-right:1px solid #dfdfdf}.coins-filter__item:hover{background-color:#c2c2c2;cursor:pointer;transition:.5s ease}.coins-filter__item--active{background-color:#c2c2c2}.coins-filter__item--active:hover{background-color:#b4b4b4}

.coins-table{display:flex;flex-direction:column;flex-grow:1;margin:0;padding:0;background-color:var(--cons-table-bg-color);border-bottom:1px solid #c7c7c7}.coins-table__items{display:flex;justify-content:center;flex-grow:1;transition:.250s ease}.coins-table__items:hover{background-color:rgba(176,196,222,0.5);transition:.250s ease}.coins-table__item{display:flex;align-items:center;flex-direction:column;padding:10px 0;padding-left:1%;padding-right:1%;font-family:'PT Sans', sans-serif;font-size:20px;line-height:22px;font-weight:400;text-align:center;font-size:16px;line-height:20px;text-align:left;border-left:1px solid #c7c7c7}.coins-table__item--active-row{background-color:rgba(219,239,255,0.5)}.coins-table__item--red{font-weight:700;color:firebrick}.coins-table__item--green{font-weight:700;color:green}.coins-table__item--hidden{color:transparent}.coins-table__item:nth-child(1){align-items:flex-start;width:8%}.coins-table__item:nth-child(2){align-items:flex-start;width:12%;font-weight:700}.coins-table__item:nth-child(3){width:10%}.coins-table__item:nth-child(4){width:14%}.coins-table__item:nth-child(5){width:14%}.coins-table__item:nth-child(6){width:15%}.coins-table__item:nth-child(7){width:8%}.coins-table__item:nth-child(8){width:8%}.coins-table__item:nth-child(9){width:10%;border-right:1px solid #c7c7c7}

.prev-next-block{display:flex;justify-content:flex-end;flex-direction:row;margin:5px 5px 5px 0}.prev-next-block__title{display:flex;justify-content:center;align-self:center;flex-grow:1;font-family:'PT Sans', sans-serif;font-size:20px;line-height:22px;font-weight:400;text-align:center;font-weight:700}.prev-next-block__button{display:flex;align-items:center;margin:0;padding:10px 25px;font-family:'PT Sans', sans-serif;font-size:20px;line-height:22px;font-weight:400;text-align:center;font-size:18px;font-weight:700;letter-spacing:2px;border:1px solid #aaa;outline:none;transition:.5s ease-in-out}.prev-next-block__button:hover{background-color:#cfcfcf;cursor:pointer;transition:.5s ease-in-out}.prev-next-block__button:disabled{background-color:transparent;cursor:default}.prev-next-block__button--prev{margin-right:5px}

.preloader{display:flex;justify-content:center}.lds-facebook{display:inline-block;position:relative;width:80px;height:80px}.lds-facebook div{display:inline-block;position:absolute;left:8px;width:16px;background:black;animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}.lds-facebook div:nth-child(1){left:8px;animation-delay:-0.24s}.lds-facebook div:nth-child(2){left:32px;animation-delay:-0.12s}.lds-facebook div:nth-child(3){left:56px;animation-delay:0}@keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}

.error-page{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh;background-image:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAKAAAACgAgMAAAD2oY++AAAACVBMVEUAAADDw8PDw8OvAWmnAAAAA3RSTlMAQAAKD4vbAAAAbUlEQVR4Ae3RsQkEMQwF0V+nCzBYBRlW+QqkKq+E/fExyeTDk1a+Pd9RV+Q2ouqJNKKsM9uIIm+3EcWqd4zoqc4w4s88c7KMKOJWG9EggwwyyCCDDDLIIIMMMsgggwwyyCCDDDLIIIMMMv8p8wNSE0cppO5PBAAAAABJRU5ErkJggg==);background-repeat:repeat;box-shadow:inset 0px 0px 250px 10px rgba(0,0,0,0.75)}.error-page__title{display:flex;margin-bottom:15px;font-size:36px;line-height:42px}.error-page__icon{display:flex;margin-bottom:15px;font-size:86px}.error-page__text{display:flex;font-size:32px;line-height:38px}

.app{display:flex;flex-direction:column;width:1200px;margin:0 auto;box-shadow:0 0 15px 10px rgba(0,0,0,0.5)}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

html{margin:0;padding:0;box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, 'PT Sans', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-style:normal;color:var(--font-color);background-color:var(--main-bg-color)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{text-decoration:none}img{display:block;max-width:100%;height:auto}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:#888}::-webkit-scrollbar-thumb:hover{background:#555}

:root{--main-bg-color: #ffffff;--font-color: #000000;--header-bg-color: rgb(241, 241, 241);--coins-filter-bg-color: rgb(223, 223, 223);--cons-table-bg-color: rgb(241, 241, 241)}

